import 'babel-polyfill'
import Vue from 'vue/dist/vue.esm.js'

const uportConnect = require('uport-connect');
const qrcode = require('qrcode-terminal');

const mnidAddress = '2ojJY9vkeJPB28URS7A3SzyWXYxdadttknb';
const signingKey = '3c60c8588c8d81124ec32d561a0cdc0ba2e094b05c48973251bf17f91a529498';
const appName = 'DID Auth PoC';

const uriHandler = (uri) => {
  qrcode.generate(uri, {small: true})
  console.log(uri)
}

const uport = new uportConnect.Connect(appName, {
    //uriHandler,
    clientId: mnidAddress,
    network: 'rinkeby',
    signer: uportConnect.SimpleSigner(signingKey)
});

new Vue({
    el: '#app',
    data: {
      user: null,
    },
    methods: {
      login: function() {
        let app = this
        // Request credentials
        uport.requestCredentials({
            requested: ['name', 'avatar', 'address', 'country', 'publicKey', 'publicEncKey'],
        }).then((credentials) => {
            app.user = credentials
            console.log(credentials);
        })
      },
      logout: function() {
        this.user = null
      },
    },
  })
  
